<template>
  <div class="shopcart-container">
    <!--  -->
    <div class="goods-list">
      <!--  -->
      <div class="mui-card">
        <div class="mui-card-content" v-for="(item) in order.user_order_goods" :key="item.id">
          <div class="mui-card-content-inner flex">
            <img :src="item.goods_goods.image" />
            <div class="info">
              <h1>{{ item.goods_goods.name }}</h1>
              <p class="flex">
                <span class="price">{{ item.price }}</span>
                <span>x{{ item.count }}</span>
              </p>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="process-info">
          <p>
            <strong></strong>
          </p>
          <p>
            <span>{{ order.note }}</span>
          </p>
        </div>
        <!--  -->
        <div class="process-info">
          <p>
            <strong></strong>
            <strong></strong>
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </div>
        <!--  -->
        <div class="process-info">
          <p>
            <strong></strong>
          </p>
          <p>
            <span>{{ order.address_name }} {{ order.address_tel }}</span>
          </p>
          <p>
            <span>{{ order.address_area }} {{ order.address_detail }}</span>
          </p>
        </div>
      </div>
    </div>
    <!--  -->
    <ul class="fare-info mui-card">
      <li class="fare-price flex">
        <span></span>
        <span class="red">{{ order.price }}</span>
      </li>
      <li class="fare-price flex">
        <span></span>
        <span class="red">0.00</span>
      </li>
      <li class="fare-price flex">
        <span>
          <strong></strong>
        </span>
        <span class="red">{{ order.price }}</span>
      </li>
      <!--  -->
      <div v-if="order.id && !order.is_cancel" class="flex">
        <button class="mui-btn mui-btn-primary mui-btn-block"></button>
      </div>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      order: {}
    }
  },
  props: ['id'],
  created () {
    this.getOrder()
  },
  methods: {
    getOrder () {
      this.$indicator.open({
        text: ''
      })
      this.$http.get('order/show', { id: this.$props.id }).then(res => {
        this.$indicator.close()
        if (res.data.code === 0) {
          this.$toast(res.data.msg)
        } else if (res.data.code === 1) {
          this.order = res.data.data
        } else if (res.data.code === 2) {
          this.$router.push({ name: 'login' })
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.flex {
  display: flex;
}
.shopcart-container {
  background: #eee;
  overflow: hidden;
  // 
  .goods-list {
    margin-top: 10px;
    .mui-card {
      margin: 0;
      .mui-card-content {
        border-bottom: 1px solid #eee;
        .mui-card-content-inner {
          align-items: center;
          padding: 10px;
          img {
            width: 60px;
          }
          .info {
            margin-left: 10px;
            box-sizing: border-box;
            width: 100%;
            overflow: hidden;
            h1 {
              font-size: 13px;
              font-weight: bold;
              line-height: 20px;
              padding-top: 22px;
            }
            p {
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              .price {
                font-size: 16px;
                font-weight: 700;
                color: red;
                flex: 1;
              }
            }
          }
        }
      }
      // 
      .process-info {
        padding: 10px;
        border-bottom: 1px solid #eee;
        p {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          color: #333;
          span {
            color: #999;
          }
        }
      }
    }
  }
  // 
  .fare-info {
    padding: 10px;
    margin: 10px 0 0 0;
    .fare-price {
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      .red {
        color: red;
        font-size: 16px;
      }
    }
  }
}
</style>
