<template>
  <div class="shopcart-container">
    <!--  -->
    <div class="address-container">
      <div class="mui-card">
        <router-link :to="{name: 'address_select'}" replace>
          <h3>{{ address.name }} {{ address.tel }}</h3>
          <p>{{ address.area }} {{ address.detail }}</p>
        </router-link>
      </div>
    </div>
    <!--  -->
    <div class="goods-list">
      <!--  -->
      <div class="mui-card">
        <div class="mui-card-content" v-for="(item) in goodslist" :key="item.id">
          <div class="mui-card-content-inner flex">
            <img :src="item.image" />
            <div class="info">
              <h1>{{ item.name }}</h1>
              <p class="flex">
                <span class="price">{{ item.price }}</span>
                <span>x{{ item.count }}</span>
              </p>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="process-info">
          <p>
            <strong></strong>
            <strong></strong>
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </div>
        <!--  -->
        <div class="store-info">
          <strong></strong>
          <textarea v-model="note" type="text" placeholder=""></textarea>
        </div>
      </div>
    </div>
    <!--  -->
    <ul class="fare-info mui-card">
      <li class="fare-price flex">
        <span></span>
        <span class="red">{{ amount.toFixed(2) }}</span>
      </li>
      <li class="fare-price flex">
        <span></span>
        <span class="red">0.00</span>
      </li>
      <li class="fare-price flex">
        <span>
          <strong></strong>
        </span>
        <span class="red">{{ amount.toFixed(2) }}</span>
      </li>
      <!--  -->
      <div class="flex">
        <button class="mui-btn mui-btn-primary mui-btn-block" @click="order"></button>
      </div>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
export default {
  data () {
    return {
      goodslist: [],
      amount: 0,
      note: '',
      address: {},
      addressId: 0
    }
  },
  computed: {
    ...mapState('shopcart', ['buy']),
    ...mapGetters('shopcart', ['getBuy'])
  },
  created () {
    if (this.$route.params.id) {
      this.addressId = this.$route.params.id
    }
    this.getAddress()
    this.getGoodsList()
  },
  methods: {
    getGoodsList () {
      var idArr = []
      this.buy.forEach(item => idArr.push(item.id))
      if (idArr.length <= 0) {
        return
      }
      this.$indicator.open({
        text: ''
      })
      var params = { ids: idArr }
      this.$http.get('shopcart', { params: params }).then(res => {
        this.$indicator.close()
        if (res.data.code === 1) {
          this.goodslist = res.data.data
          var amount = 0
          this.goodslist.forEach(item => {
            item.count = this.getBuy[item.id].count
            amount += item.count * item.price
          })
          this.amount = amount
        }
      })
    },
    getAddress () {
      var params = { id: this.addressId }
      this.$http.get('address/def', { params: params }).then(res => {
        if (res.data.code === 0) {
          this.$toast(res.data.msg)
        } else if (res.data.code === 1) {
          if (res.data.data) {
            this.address = res.data.data
            this.addressId = res.data.data.id
          } else {
            this.$toast('')
            this.$router.replace({ name: 'address_select' })
          }
        } else if (res.data.code === 2) {
          this.$router.push({ name: 'login' })
        }
      })
    },
    order () {
      var form = {
        address: this.addressId,
        goods: this.buy,
        note: this.note
      }
      this.$indicator.open({
        text: ''
      })
      this.$http.post('order/create', form).then(res => {
        this.$indicator.close()
        window.console.log(res.data)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.flex {
  display: flex;
}
.shopcart-container {
  background: #eee;
  overflow: hidden;
  // 
  .goods-list {
    margin-top: 10px;
    .mui-card {
      margin: 0;
      .mui-card-content {
        border-bottom: 1px solid #eee;
        .mui-card-content-inner {
          align-items: center;
          padding: 10px;
          img {
            width: 60px;
          }
          .info {
            margin-left: 10px;
            box-sizing: border-box;
            width: 100%;
            overflow: hidden;
            h1 {
              font-size: 13px;
              font-weight: bold;
              line-height: 20px;
              padding-top: 22px;
            }
            p {
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              .price {
                font-size: 16px;
                font-weight: 700;
                color: red;
                flex: 1;
              }
            }
          }
        }
      }
      // 
      .process-info {
        padding: 10px;
        border-bottom: 1px solid #eee;
        p {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          color: #333;
          span {
            color: #999;
          }
        }
      }
      // 
      .store-info {
        padding: 10px;
        textarea {
          font-size: 13px;
        }
      }
    }
  }
  // 
  .fare-info {
    padding: 10px;
    margin: 10px 0 0 0;
    .fare-price {
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      .red {
        color: red;
        font-size: 16px;
      }
    }
  }
  // 
  .address-container {
    .mui-card {
      margin: 0;
      padding: 10px;
      h3 {
        color: #333;
        font-size: 16px;
        font-weight: bold;
      }
    }
  }
}
</style>
