<template>
  <div class="photo-con">
    <!--  -->
    <div id="slider" class="mui-slider">
      <div id="sliderSegmentedControl"
        class="mui-scroll-wrapper mui-slider-indicator mui-segmented-control mui-segmented-control-inverted">
        <div class="mui-scroll">
          <a :class="['mui-control-item', cateId == item.id ? 'mui-active': '']" v-for="item in cates" :key="item.id"
            @tap="getPhotoListByCateId(item.id)">
            {{item.title}}
          </a>
          <!-- tapmui -->
        </div>
      </div>
    </div>
    <!--  -->
    <ul class="photo-list">
      <router-link v-for="item in list" :key="item.id" :to="{ name: 'photo_show', params: { id: item.id }}" tag="li">
        <img v-lazy="item.img_url"> <!--  -->
        <div class="info">
          <h1 class="info-title">{{ item.title }}</h1>
          <div class="info-body">{{ item.zhaiyao }}</div>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
// muijs
import mui from '../../lib/mui/js/mui.min.js'
export default {
  data () {
    return {
      cates: [],// 
      list: [], // 
      cateId: 0
    }
  },
  // data  message
  created () {
    // 
    this.getAllCategory();
    this.getPhotoListByCateId(0)
  },
  // dom
  mounted () {
    // scroll
    mui('.mui-scroll-wrapper').scroll({
      deceleration: 0.0005 //flick 0.0006
    });
  },
  methods: {
    // 
    getAllCategory () {
      this.$http.get('photo/getimgcategory').then(result => {
        if (result.data.status === 0) {
          // 
          // unshift()
          result.data.message.unshift({ title: '', id: 0 });
          this.cates = result.data.message
        } else {
          this.$toast('')
        }
      })
    },
    getPhotoListByCateId (cateId) {
      this.cateId = cateId
      // Id,
      this.$http.get('photo/getimages/', { params: {id: cateId} }).then(result => {
        if (result.data.status === 0) {
          this.list = result.data.message
        }
      })
    }
  }
}
</script>

<style lang="scss">
* {
  touch-action: pan-y; /**/
}
.photo-list {
  list-style: none;
  margin: 0;
  padding: 10px;
  padding-bottom: 0;
  li {
    background-color: #ccc;
    text-align: center;
    margin-bottom: 5px;
    box-shadow: 0 0 9px #999;
    position: relative;
    img {
      width: 100%;
      vertical-align: middle;
    }
    img[lazy="loading"] {
      width: 40px;
      height: 300px;
      margin: auto;
    }
    .info {
      color: white;
      text-align: left;
      position: absolute;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.4);
      max-height: 84px;
      .info-title {
        font-size: 14px;
      }
      .info-body {
        font-size: 13px;
      }
    }
  }
}
</style>

