<template>
  <div class="goods-info">
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <swiper :imgList="goodsinfo.album"></swiper>
        </div>
      </div>
    </div>
    <!--  -->
    <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="ballFlag" ref="ball"></div>
    </transition>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header">{{ goodsinfo.name }}</div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p class="price">
            
            <span>{{ goodsinfo.price }}</span>
          </p>
          <div v-if="goodsinfo.num">
            <p class="go-num">
              
              <numbox @count="countChange" :max="goodsinfo.num" initcount="1" :goodsid="goodsinfo.id"></numbox>
            </p>
            <p class="go-buy">
              <mt-button type="primary" size="small" @click="buy"></mt-button>
              <mt-button type="danger" size="small" @click="addShopcart"></mt-button>
            </p>
          </div>
          <div v-else></div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="mui-card">
      <div class="mui-card-header"></div>
      <div class="mui-card-content">
        <div class="mui-card-content-inner">
          <p>{{ goodsinfo.sell_point }}</p>
          <p>{{ goodsinfo.num }}</p>
          <p>{{ goodsinfo.create_time }}</p>
        </div>
      </div>
      <div class="mui-card-header"></div>
      <div class="good-desc">
        <div class="content" v-html="goodsinfo.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
import swiper from '../../components/swiper.vue'
import numbox from '../../components/numbox.vue'

export default {
  data () {
    return {
      goodsinfo: {},     // 
      selectedCount: 1,  // 1
      ballFlag: false,   // 
    }
  },
  props: ['id'],
  created () {
    this.getGoodsInfo()
  },
  methods: {
    getGoodsInfo () {
      this.$indicator.open({
        text: ''
      })
      let params = { id: this.id }
      this.$http.get('goodsinfo', { params: params }).then(res => {
        this.$indicator.close()
        if (res.data.code === 0) {
          this.$toast(res.data.msg)
        } else if (res.data.code === 1) {
          if (res.data.data) {
            this.goodsinfo = res.data.data
          } else {
            this.$messagebox.alert('').then(() => {
              this.$router.go(-1)
            })
          }
        } else {
          this.$toast('')
        }
      })
    },
    countChange (goodsinfo) {
      this.selectedCount = goodsinfo.count
    },
    addShopcart () {
      this.ballFlag = !this.ballFlag
      this.$store.commit('shopcart/addCar', {
        id: this.id,
        count: this.selectedCount,
        selected: true
      })
    },
    // -
    beforeEnter (el) {
      el.style.transform = 'translate(0,0)'
    },
    // -
    enter (el, done) {
      el.offsetWidth // 
      // 
      const ballPosition = this.$refs.ball.getBoundingClientRect();
      // 
      const badgePosition = document.getElementById('badge').getBoundingClientRect()
      const xDist = badgePosition.left - ballPosition.left
      const yDist = badgePosition.top - ballPosition.top
      el.style.transform = `translate(${xDist}px,${yDist}px)`;
      el.style.transition = 'all .5s cubic-bezier(.4, -0.3, 1, .68)'
      done()
    },
    // -
    afterEnter () {
      this.ballFlag = !this.ballFlag
    },
    buy () {
      this.$store.commit('shopcart/setBuy', { id: this.$props.id, count: this.selectedCount })
      this.$router.push({ name: 'order_create' })
    }
  },
  components: {
    swiper,
    numbox
  }
}
</script>

<style lang="scss" scoped>
.goods-info {
  background: #f1f1ff;
  overflow: hidden;
  .price {
    span {
      color: red;
      font-size: 14px;
      font-weight: bold;
    }
  }
  .go-buy {
    display: flex;
    margin: 10px 0 0px;
    justify-content: center;
    button {
      margin: 0 5px;
    }
  }
  .good-desc {
    background: #fff;
    padding: 5px;
    h3 {
      font-size: 16px;
      color: #226aff;
      text-align: center;
      margin: 15px 0;
    }
    .content {
      font-size: 14px;
      line-height: 28px;
      img {
        width: 100%;
      }
    }
  }
  .go-num {
    div {
      margin-left: 10px;
    }
  }
  .ball {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    position: absolute;
    background: red;
    z-index: 99;
    left: 132px;
    top: 390px;
    transform: translate(93px, 230px);
  }
}
</style>
